* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.switcher-wrapper {
  display: flex;
  justify-content: center;
}

.switcher {
  padding: 15px 45px;
  margin-right: 20px;

  font-size: 26px;
}

.isActive {
  color: #949494;
  border: 1px solid #949494;
  border-radius: 2px;
  background-color: rgb(218, 218, 218);
}

.coundown-input {
  font-size: 26px;
}

.timer {
  display: flex;
  justify-content: left;
}

.timer .field {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  margin-right: 25px;

  font-weight: 500;
}

.timer .field .value {
  font-size: 62px;
}

.timer .label {
  text-transform: uppercase;
  font-size: 20px;
}

.coundown-input-btn {
  font-size: 26px;
}
